export const productCart = () => {
    let product = window.localStorage.getItem('TB0pQDcfjymepm6vmhwp66LASgLgSHaoDU2');
    if (product && typeof product !== 'undefined' && product !== 'undefined') {
        return JSON.parse(product);
    } else {
        return [];
    }
};

export const updateCart = (products) => {
    window.localStorage.setItem('TB0pQDcfjymepm6vmhwp66LASgLgSHaoDU2', JSON.stringify(products));
};

export const clearProductCart = () => {
    window.localStorage.removeItem('TB0pQDcfjymepm6vmhwp66LASgLgSHaoDU2');
};

export const customer_address = () => {
    let address = window.localStorage.getItem('_shipping_address');
    if (address && typeof address !== 'undefined' && address !== 'undefined') {
        return JSON.parse(address);
    } else {
        return {};
    }
};

export const update_customer_address = (address) => {
    window.localStorage.setItem('_shipping_address', JSON.stringify(address));
};


export const convertedPrice = (price) => {
    let rate = Number(b2b.increase_rate);
    return Number(Number(price) * rate).toFixed(2);
};


export const calculateAirShippingCharge = (subTotal) => {
    let shipping_charges = b2b.air_shipping_charges ? JSON.parse(b2b.air_shipping_charges) : {};
    let airLength = shipping_charges.length;
    let chargeAmount = 800;
    let dCharge = null;
    if (airLength) {
        for (let i = 0; i < airLength; i++) {
            dCharge = shipping_charges[i];
            if (dCharge.minimum < subTotal && dCharge.maximum > subTotal) {
                chargeAmount = dCharge.rate;
                break;
            }
        }
    }
    return chargeAmount;
};

export const remove_space = (stringData) => {
    return stringData.trim() // remove white spaces at the start and end of string
        .toLowerCase() // string will be lowercase
        .replace(/^-+/g, "") // remove one or more dash at the start of the string
        .replace(/[^\w-]+/g, "-") // convert any on-alphanumeric character to a dash
        .replace(/-+/g, "-") // convert consecutive dashes to singular one
        .replace(/-+$/g, "");
};


export const QuantityRangesPrice = (ranges) => {
    let quantity = $(document).find('#totalQuantity').text();
    let unitPrice = 0;
    quantity = Number(quantity);
    if (ranges) {
        for (let i = 0; i < ranges.length; i++) {
            let QtyRange = ranges[i];
            let nextQtyRange = ranges[i + 1];
            let MinQuantity = QtyRange.MinQuantity;
            if (quantity < MinQuantity) {
                unitPrice = QtyRange.Price.OriginalPrice;
                break;
            } else {
                if (nextQtyRange) {
                    nextQtyRange = nextQtyRange.MinQuantity;
                    if (MinQuantity <= quantity && quantity < nextQtyRange) {
                        unitPrice = QtyRange.Price.OriginalPrice;
                        break;
                    }
                } else {
                    unitPrice = QtyRange.Price.OriginalPrice;
                }
            }
        }
    }
    unitPrice = convertedPrice(unitPrice);
    if (Number(unitPrice)) {
        $(document).find('#itemCalculationTable').find('.priceRate').text(unitPrice);
    }
    return Number(unitPrice);
};


export const check_cart = (item_id) => {
    let product_cart = productCart();
    if (product_cart.length) {
        let cartItem = product_cart.find((existsItem) => existsItem.Id === item_id);
        if (cartItem) {
            if (cartItem.isCart === true) {
                $(document).find('#buyNow').show();
                $(document).find('.btn-addToCart').hide();
            } else {
                $(document).find('#buyNow').hide();
                $(document).find('.btn-addToCart').show();
            }
        }
        const cart = product_cart.filter((findCart) => findCart.isCart === true);
        if (cart.length) {
            $(document).find('.cart_trigger').find('.cart_count').text(cart.length);
        }
    }
}


export const validateCartProducts = (proceedLink = null) => {
    let products = productCart().filter((filterCart) => filterCart.isCart === true);
    if (products.length) {

        let minQtyOnSetting = Number(b2b.min_order_quantity);
        let minAmount = Number(b2b.min_order_amount);
        let currency_icon = b2b.currency_icon;

        let proceed = true;
        for (let i = 0; products.length > i; i++) {
            let product = products[i];
            let minQuantity = product.item.FirstLotQuantity;
            let Title = product.item.Title;
            let itemQuantity = 0;
            let itemSubTotal = 0;
            product.itemData.map((singleItemData) => {
                itemQuantity += singleItemData.quantity;
                itemSubTotal += singleItemData.subTotal;
            });

            let $table = `<table class="table table-bordered table-sm">
                            <tr>
                              <td class="align-middle">
                                <img src="${product.item.MainPictureUrl}" style="width:150px">
                              </td>
                              <td class="align-middle">
                                <span class="text-danger">${Title}</span>
                              </td>
                            </tr>
                          </table>`;

            if ((minQuantity > itemQuantity) || (minQtyOnSetting > itemQuantity)) {
                let takenQty = minQtyOnSetting > minQuantity ? minQtyOnSetting : minQuantity;
                Swal.fire({
                    // icon: 'warning',
                    title: 'You can\'t Down the Minimum Quantity of ' + takenQty,
                    html: $table,
                });
                proceed = false;
                $(document).find('#' + product.Id).addClass('text-danger');
                break;
            }

            if (itemSubTotal < minAmount) {
                Swal.fire({
                    // icon: 'warning',
                    title: `You can't down the minimum price of ${currency_icon} ${minAmount.toFixed(2)}`,
                    html: $table,
                });
                proceed = false;
                $(document).find('#' + product.Id).addClass('text-danger');
                break;
            }
        }

        if (proceed && proceedLink) {
            window.location.href = proceedLink;
        }
        return proceed;
    }
}


