import {
    productCart,
    update_customer_checkout,
    customer_address,
    clearProductCart,
    validateCartProducts
} from "./cartHelpers";

function loadProductWithAttributes(singleItem, attributes, product) {
    let attrLength = attributes.length + 3;
    let htmlData = "";
    attributes.map((attribute, key) => {
        let $PropertyName = attribute.PropertyName;
        if (key === 0) {
            htmlData += `<tr>
                          <td rowspan="${attrLength}" class="align-middle text-center">
                              <a href="/product/${product.Id}"> <img src="${
                singleItem.image
            }" style="width: 110px"></a>
                          </td>
                          <td class="text-capitalize text-left text-break">${$PropertyName}</td>
                          <td class="text-right text-break">${
                              attribute.Value
                          }</td>
                          <td rowspan="${attrLength}" class="text-center align-middle">${
                singleItem.quantity
            }</td>
                          <td rowspan="${attrLength}" class="text-right align-middle">
                              <span class="SingleTotal">${
                                  b2b.currency_icon
                              } ${Number(singleItem.subTotal).toFixed(2)}</span>
                          </td>
                          </tr>`;
        } else {
            htmlData += `<tr><td class="text-capitalize text-left">${$PropertyName}</td><td class="text-right">${attribute.Value}</td></tr>`;
        }
    });

    let approxWeight = singleItem.quantity * product.ActualWeightInfo.Weight;

    return (
        htmlData +
        `<tr>
                          <td class="align-middle text-left" style="min-width:100px">Shipping Method:</td>
                          <td class="text-danger text-right">${
                              product.shipped_by
                          } - ${b2b.currency_icon} ${
            product.shippingRate
        } Per KG</td>
                      </tr>
                      <tr>
                          <td class="align-middle text-left" style="min-width:135px">Approx weight:</td>
                          <td class="text-right" style="min-width:155px"><span class="approxWeight">${Number(
                              approxWeight
                          ).toFixed(2)}</span> KG</td>
                      </tr>
                      <tr>
                          <td class="align-middle" style="min-width:100px">Per unit Price</td>
                          <td class="text-right">${
                              b2b.currency_icon
                          } <span class="unitPrice">${
            singleItem.price
        }</span></td>
                      </tr>`
    );
}

function loadProductExceptAttributes(singleItem, product) {
    let approxWeight = singleItem.quantity * product.ActualWeightInfo.Weight;
    return `<tr>
              <td rowspan="3">
              <a href="/product/${product.Id}"> <img src="${
        product.item.MainPictureUrl
    }"  style="width: 110px"></a>
              </td>
              <td>Shipping Method:</td>
              <td>${product.shipped_by} BDT - ${
        product.shippingRate
    } Per KG</td>
              <td rowspan="3" class="text-center align-middle">${
                  singleItem.quantity
              }</td>
              <td rowspan="3" class="text-right align-middle">
                 <span class="SingleTotal">${b2b.currency_icon} ${Number(
        singleItem.subTotal
    ).toFixed(2)}</span>
              </td>
          </tr>
          <tr>
              <td>Approx weight:</td>
              <td><span class="approxWeight">${Number(approxWeight).toFixed(
                  2
              )}</span> KG</td>
          </tr>
          <tr>
              <td>Per unit Price</td>
              <td>${b2b.currency_icon} <span class="unitPrice">${
        singleItem.price
    }</span></td>
          </tr>`;
}

function loaded_payment_cart() {
    let product = productCart();
    let cartProducts = product.filter(filterCart => filterCart.isCart === true);
    if (cartProducts.length) {
        let htmlData = "";
        cartProducts.map(product => {
            let item = product.item;
            let itemData = product.itemData;
            let totalItemQty = 0;
            let totalItemPrice = 0;
            htmlData += `<tr><td class="text-left" colspan="5"><a href="/product/${product.Id}" id="${product.Id}">${item.Title}</a></td></tr>`;
            itemData.map((singleItem, key) => {
                let attributes = singleItem.attributes;
                let sinQuantity = singleItem.quantity;
                let subTotal = singleItem.subTotal;
                totalItemQty += Number(sinQuantity);
                totalItemPrice += Number(subTotal);
                if (attributes.length) {
                    htmlData += loadProductWithAttributes(
                        singleItem,
                        attributes,
                        product
                    );
                } else {
                    htmlData += loadProductExceptAttributes(
                        singleItem,
                        product
                    );
                }
            });

            let localDelivery = Number(product.localDelivery);
            htmlData += `<tr>
                <td class="text-right" colspan="3">China Local Delivery</td>
                <td class="text-center">-</td><td class="text-right">${
                    b2b.currency_icon
                } ${localDelivery.toFixed(2)}</td></tr>`;
            totalItemPrice = localDelivery + totalItemPrice;

            htmlData += `<tr>
              <td class="text-right" colspan="3">Sub Total</td>
              <td class="text-center">${totalItemQty}</td><td class="text-right">${
                b2b.currency_icon
            } <span class="totalItemPrice">${totalItemPrice.toFixed(
                2
            )}</span></td></tr>`;
        });
        $(document)
            .find(".payment_cart_table")
            .find("tbody")
            .html(htmlData);
    }
}

function nagad_payment_process(tran_id, amount) {
    $.ajax({
        url: "/nagad-payment-process",
        method: "POST",
        data: {
            tran_id: tran_id,
            amount: amount
        },
        dataType: "json",
        headers: {
            "X-CSRF-TOKEN": $('meta[name="csrf-token"]').attr("content")
        },
        success: function(res) {
            console.log(res);
            if (res.status) {
                window.open(res.redirectUrl, "_self");
                // window.location.replace(res.redirectUrl);
                // window.location.assign(res.redirectUrl);
            }
        },
        error: function(xhr) {
            console.log(xhr);
        },
        complete: function() {
            console.log("complete");
        }
    });
}

function sslCommerzPaymentProcess(tran_id) {
    $.ajax({
        url: "/sslcommerz/payment",
        method: "POST",
        data: {
            tran_id: tran_id
        },
        dataType: "json",
        headers: {
            "X-CSRF-TOKEN": $('meta[name="csrf-token"]').attr("content")
        },
        success: function(response) {
            if (response.status === "success") {
                // window.location.replace(response.data);
                window.location.assign(response.data);
            } else {
                Swal.fire({
                    icon: "warning",
                    text: "Payment process error, try again"
                });
                console.log(response);
            }
        },
        error: function(xhr) {
            console.log(xhr);
        },
        complete: function() {
            console.log("complete");
        }
    });
}

function load_the_incomplete_payment(pay_method) {
    let amount = $(document)
        .find("#needToaPayAmount")
        .text();
    let tran_id = $("#incompletePayNowBtn").attr("data-order");
    if (pay_method === "bkash") {
        window.location.replace("/bkash/payment");
    } else if (pay_method === "nagad") {
        nagad_payment_process(tran_id, amount);
    } else if (pay_method === "ssl-commerz") {
        sslCommerzPaymentProcess(tran_id);
    } else if (pay_method === "smanager") {
        s_manager_payment_process(tran_id);
    }
}

function s_manager_payment_process(transaction_id) {
    axios
        .post("/ajax/s-manager-payment", { tran_id: transaction_id })
        .then(response => {
            let resData = response.data;
            if (resData.status) {
                // window.location.replace(resData.redirect);
                window.location.assign(resData.redirect);
            } else {
                Swal.fire({
                    icon: "warning",
                    text: resData.message,
                    customClass: {
                        icon: "my-2",
                        actions: "shadow-none",
                        confirmButton: "shadow-none",
                        denyButton: "shadow-none",
                        cancelButton: "shadow-none"
                    }
                });
            }
        });
}

function load_the_payment_confirm(pay_method) {
    let OrderItem = productCart().filter(cart => cart.isCart === true);
    let address = customer_address();
    let summary = window.localStorage.getItem("summary");
    summary = JSON.parse(summary);
    let tran_id = $("#payNowBtn").attr("data-order");
    $.ajax({
        url: "/ajax/order-confirm",
        method: "POST",
        data: {
            order_id: tran_id,
            pay_method: pay_method,
            OrderItem: JSON.stringify(OrderItem),
            summary: JSON.stringify(summary),
            address: JSON.stringify(address)
        },
        dataType: "json",
        headers: {
            "X-CSRF-TOKEN": $('meta[name="csrf-token"]').attr("content")
        }
    })
        .done(function(response) {
            if (response.status) {
                // clearProductCart();
                if (pay_method === "bkash") {
                    window.location.replace("/bkash/payment");
                } else if (pay_method === "nagad") {
                    let summary = response.summary;
                    let amount = summary.needToPay;
                    nagad_payment_process(tran_id, amount);
                } else if (pay_method === "ssl-commerz") {
                    sslCommerzPaymentProcess(response.tran_id);
                } else if (pay_method === "smanager") {
                    s_manager_payment_process(response.tran_id);
                }
            }
        })
        .fail(function(response) {
            Swal.fire({
                icon: "warning",
                text: "Your Order Not Placed!"
            });
        });
}

function payment_summary() {
    let currency_icon = b2b.currency_icon;
    let summary = window.localStorage.getItem("summary");
    summary = JSON.parse(summary);
    $(document)
        .find("#productTotalPrice")
        .text(`${currency_icon} ` + Number(summary.productTotal).toFixed(2));
    $(document)
        .find("#couponDiscount")
        .text(`${currency_icon} ` + Number(summary.couponDiscount).toFixed(2));
    $(document)
        .find("#needToPay")
        .text(`${currency_icon} ` + Number(summary.needToPay).toFixed(2));
    $(document)
        .find("#dueForProducts")
        .text(`${currency_icon} ` + Number(summary.dueForProducts).toFixed(2));

    let couponRow = $(document).find("#couponRow");
    if (summary.couponDiscount) {
        couponRow.show();
    } else {
        couponRow.hide();
    }
}

$(document).on("click", ".payment_label", function() {
    $(".payment_label").removeClass("active");
    $(this).addClass("active");
    let method = $(this).attr("for");
    $("#payment_method").val(method);
});

$(document).on("change", "#termsField", function() {
    let formCheck = $(this)
        .closest(".form-check")
        .find("p");
    if ($(this).is(":checked")) {
        formCheck.hide();
    } else {
        formCheck.show();
    }
});

$(document).on("click", "#payNowBtn", function() {
    let termsField = $(document).find("#termsField");
    // let pay_method = $(document).find('#payment_method').val();
    let pay_method = "smanager";
    let proceed = validateCartProducts();
    if (proceed) {
        if (!pay_method) {
            Swal.fire({
                icon: "warning",
                text: "Select a payment method first"
            });
        } else {
            if (termsField.is(":checked")) {
                load_the_payment_confirm(pay_method);
            } else {
                Swal.fire({
                    icon: "warning",
                    text:
                        "Please Check, Read and agree to the website Terms and Conditions and Refund Policy"
                });
            }
        }
    }
});

$(document).on("click", "#incompletePayNowBtn", function() {
    let termsField = $(document).find("#termsField");
    // let pay_method = $(document)
    //     .find("#payment_method")
    //     .val();
    let pay_method = "smanager";
    if (!pay_method) {
        Swal.fire({
            icon: "warning",
            text: "Select a payment method first"
        });
    } else {
        if (termsField.is(":checked")) {
            load_the_incomplete_payment(pay_method);
        } else {
            Swal.fire({
                icon: "warning",
                text:
                    "Please Check, Read and agree to the website Terms and Conditions and Refund Policy"
            });
        }
    }
});

$(function() {
    let page = $(document)
        .find(".section")
        .attr("data-page");
    if (page === "payment") {
        let products = productCart();
        if (products.length === 0) {
            window.location.href = "/dashboard?tab=orders";
        }
        payment_summary();
        loaded_payment_cart();
        validateCartProducts();
        update_customer_checkout();
    }
});
