function productId(fullInfo = false) {
    if (fullInfo) {
        let product = $(document)
            .find("#FupQl1JvvQsfpyCgZP2N")
            .attr("data-j0HFfdMnMK5ktCRMHvKK");
        if (product) {
            let fullProduct = JSON.parse(decodeURIComponent(product));
            return {
                Id: fullProduct.Id,
                Title: fullProduct.Title,
                MainPictureUrl: fullProduct.MainPictureUrl,
                ActualWeightInfo: fullProduct.ActualWeightInfo,
                BatchLotQuantity: fullProduct.BatchLotQuantity,
                FirstLotQuantity: fullProduct.FirstLotQuantity,
                NextLotQuantity: fullProduct.NextLotQuantity
            };
        }
    } else {
        return $(document)
            .find("#itemFullInfo")
            .attr("data-id");
    }
}

function loadPrices(reload = 0) {
    var priceUrl = $(document)
        .find(".main_content")
        .attr("data-cO7QTHpkoZrKge5aGkDc");
    var item_id = $(document)
        .find("#itemFullInfo")
        .attr("data-id");
    var priceBox = $(".mainPrice");
    $.ajax({
        url: priceUrl,
        method: "POST",
        data: {
            item_id: item_id
        },
        dataType: "html",
        headers: {
            "X-CSRF-TOKEN": $('meta[name="csrf-token"]').attr("content")
        },
        beforeSend: function() {
            priceBox.html(loadingText);
        },
        success: function(priceData) {
            if (priceData === "reload") {
                if (reload < 3) {
                    loadPrices(reload + 1);
                }
            } else {
                priceBox.html(priceData);
                let findItem = $(document).find("#FupQl1JvvQsfpyCgZP2N");
                let itemData = findItem.attr("data-j0hffdmnmk5ktcrmhvkk");
                itemData = JSON.parse(itemData);
                findItem.attr(
                    "data-j0hffdmnmk5ktcrmhvkk",
                    encodeURIComponent(JSON.stringify(itemData))
                );
            }
        },
        error: function(xhr) {
            if (reload < 3) {
                loadPrices(reload + 1);
            } else {
                priceBox.html(loadingFail);
            }
        },
        complete: function() {
            console.log("complete");
        }
    });
}

