function loadItemCalculation(reload = 0) {
    var configUrl = $(document)
        .find(".main_content")
        .attr("data-6qf0fxG8AjIZhSVmNFC4");
    var item_id = $(document)
        .find("#itemFullInfo")
        .attr("data-id");
    var itemCalculation = $(".itemCalculationTable");
    $.ajax({
        url: configUrl,
        method: "POST",
        data: {
            item_id: item_id
        },
        dataType: "html",
        headers: {
            "X-CSRF-TOKEN": $('meta[name="csrf-token"]').attr("content")
        },
        beforeSend: function() {
            itemCalculation.html(loadingText);
        }
    })
        .done(function(resData) {
            if (resData === "reload") {
                if (reload < 3) {
                    loadItemCalculation(reload + 1);
                }
            } else {
                itemCalculation.html(resData);
                setTimeout(function() {
                    filter_attribute();
                    reload_quantity_from_Cart();
                }, 500);
            }
        })
        .fail(function(response) {
            if (reload < 3) {
                loadItemCalculation(reload + 1);
            } else {
                itemCalculation.html(loadingFail);
            }
        });
}

